*{
  margin: 0;
	font-family: 'Syne', sans-serif !important;

  padding: 0;
  
}
body{
	font-family: 'Syne', sans-serif !important;
	overflow-x: hidden;
	color:#f4f4f4;
}
canvas{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
}


.subtitle{
	display: inline-block;
	font-size: 6rem;
	position:fixed;
	opacity: 0.05;
	z-index: 6;
	font-weight: 700;
	left:50%;
	text-align: center;
	width:100%;
	transform: translateX(-50%);
	color: transparent;
	line-height: 1;
	bottom:2px;
	-webkit-text-stroke: 2px #fff;
}

.stroke {
	display: inline-block;
	color: transparent;
	-webkit-text-stroke: 2px #ff0000;
	transition:0.3s all ease-in;
	
}
.stroke:hover {
	color:#ff0000;
}

.char {
	display: inline-block;
}